"use client";

import { useCallback, useEffect, useState } from "react";
import { useRouter } from "next/navigation";
import { supabase } from "@/lib/supabaseClient";
import { getPrefetchedAvatars, clearPrefetchCache } from "@/lib/prefetchAvatars";

import { CosmoLoader } from "@/components/ui/CosmoLoader";
import { AvatarUploadModal } from "@/app/avatars/components/AvatarUploadModal";
import { PhotoAvatarGrid } from "@/app/avatars/components/PhotoAvatarGrid";
import { AvatarsEmptyState } from "./AvatarsEmptyState";
import type { PhotoAvatarRow } from "@/app/avatars/servers/UploadAvatar";

export function AvatarsSection() {
  const router = useRouter();
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [isFetchingAvatars, setIsFetchingAvatars] = useState(true);
  const [photoAvatars, setPhotoAvatars] = useState<PhotoAvatarRow[]>([]);
  const [currentUserId, setCurrentUserId] = useState<string | null>(null);

  useEffect(() => {
    let isMounted = true;

    const fetchPhotoAvatars = async () => {
      const prefetchedData = getPrefetchedAvatars();
      if (prefetchedData) {
        setPhotoAvatars(prefetchedData);
        setIsFetchingAvatars(false);
        clearPrefetchCache();
        
        const {
          data: { user },
        } = await supabase.auth.getUser();
        
        if (!isMounted) return;
        
        if (!user) {
          router.push("/");
          setCurrentUserId(null);
          setPhotoAvatars([]);
          return;
        }
        
        setCurrentUserId(user.id);
        return;
      }

      setIsFetchingAvatars(true);

      const {
        data: { user },
      } = await supabase.auth.getUser();

      if (!isMounted) return;

      if (!user) {
        router.push("/");
        setCurrentUserId(null);
        setPhotoAvatars([]);
        setIsFetchingAvatars(false);
        return;
      }

      setCurrentUserId(user.id);

      const { data, error } = await supabase
        .from("photo_avatars")
        .select("*")
        .eq("uid", user.id)
        .order("created_at", { ascending: false });

      if (!isMounted) return;

      if (error) {
        console.error("[avatars] Ошибка загрузки аватаров:", error);
      } else {
        setPhotoAvatars(data ?? []);
      }
      setIsFetchingAvatars(false);
    };

    fetchPhotoAvatars();

    return () => {
      isMounted = false;
    };
  }, [router]);

  const refetchPhotoAvatars = useCallback(async () => {
    if (!currentUserId) return;

    clearPrefetchCache();

    const { data, error } = await supabase
      .from("photo_avatars")
      .select("*")
      .eq("uid", currentUserId)
      .order("created_at", { ascending: false });

    if (error) {
      console.error("[avatars] Ошибка загрузки аватаров:", error);
    } else {
      setPhotoAvatars(data ?? []);
    }
  }, [currentUserId]);

  const hasPhotoAvatars = photoAvatars.length > 0;

  return (
    <section className="mb-12">
      <div className="mb-6 flex items-center justify-between">
        <h2 className="text-2xl font-bold text-gray-900">Мои аватары</h2>
        {hasPhotoAvatars && (
          <button
            type="button"
            onClick={() => setIsModalOpen(true)}
            className="min-w-[170px] rounded-lg bg-blue-600 px-4 py-3 text-sm font-semibold text-white transition hover:bg-blue-500"
          >
            Создать аватар
          </button>
        )}
      </div>
      {isFetchingAvatars ? (
        <div className="flex min-h-[300px] items-center justify-center rounded-3xl border border-gray-100 bg-gray-50">
          <CosmoLoader ariaLabel="Загружаем фото-аватары..." />
        </div>
      ) : !hasPhotoAvatars ? (
        <AvatarsEmptyState onCreateClick={() => setIsModalOpen(true)} />
      ) : (
        <PhotoAvatarGrid
          avatars={photoAvatars}
          currentUserId={currentUserId}
          onAvatarDeleted={refetchPhotoAvatars}
        />
      )}
      <AvatarUploadModal
        isOpen={isModalOpen}
        onClose={() => setIsModalOpen(false)}
        onAvatarUploaded={refetchPhotoAvatars}
      />
    </section>
  );
}

