"use client";

import { useCallback, useEffect, useState } from "react";
import { useRouter } from "next/navigation";
import { supabase } from "@/lib/supabaseClient";

import { CosmoLoader } from "@/components/ui/CosmoLoader";
import { VoiceCreateModal } from "@/app/voices/components/VoiceCreateModal";
import { VoiceGrid } from "@/app/voices/components/VoiceGrid";
import { VoicesEmptyState } from "./VoicesEmptyState";

interface VoiceRow {
  id?: string;
  uid: string;
  name: string;
  url: string;
  created_at?: string;
  description?: string | null;
}

export function VoicesSection() {
  const router = useRouter();
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [isFetchingVoices, setIsFetchingVoices] = useState(true);
  const [voices, setVoices] = useState<VoiceRow[]>([]);
  const [currentUserId, setCurrentUserId] = useState<string | null>(null);

  const fetchVoices = useCallback(async () => {
    setIsFetchingVoices(true);

    const {
      data: { user },
    } = await supabase.auth.getUser();

    if (!user) {
      router.push("/");
      setCurrentUserId(null);
      setVoices([]);
      setIsFetchingVoices(false);
      return;
    }

    setCurrentUserId(user.id);

    const { data, error } = await supabase
      .from("voices")
      .select("*")
      .eq("uid", user.id)
      .order("created_at", { ascending: false });

    if (error) {
      console.error("[voices] Ошибка загрузки голосов:", error);
      setVoices([]);
    } else {
      console.log("[voices] Загружено голосов:", data?.length ?? 0, data);
      const validVoices = (data ?? []).filter((voice) => voice.url && voice.name);
      console.log("[voices] Валидных голосов:", validVoices.length);
      setVoices(validVoices);
    }
    setIsFetchingVoices(false);
  }, [router]);

  useEffect(() => {
    fetchVoices();
  }, [fetchVoices]);

  return (
    <section className="mb-12">
      <div className="mb-6 flex items-center justify-between">
        <h2 className="text-2xl font-bold text-gray-900">Мои голоса</h2>
        <button
          type="button"
          onClick={() => setIsModalOpen(true)}
          className="min-w-[170px] rounded-lg bg-purple-600 px-4 py-3 text-sm font-semibold text-white transition hover:bg-purple-500"
        >
          Создать голос
        </button>
      </div>
      {isFetchingVoices ? (
        <EmptyState title="" description="">
          <div className="mt-px flex justify-center">
            <CosmoLoader ariaLabel="Загружаем голоса..." />
          </div>
        </EmptyState>
      ) : voices.length === 0 ? (
        <EmptyState />
      ) : (
        <VoiceGrid
          voices={voices}
          currentUserId={currentUserId}
          onVoicesChanged={fetchVoices}
        />
      )}
      <VoiceCreateModal
        isOpen={isModalOpen}
        onClose={() => setIsModalOpen(false)}
        onVoiceCreated={fetchVoices}
      />
    </section>
  );
}

