"use client";

interface AvatarsEmptyStateProps {
  onCreateClick: () => void;
}

export function AvatarsEmptyState({ onCreateClick }: AvatarsEmptyStateProps) {
  return (
    <div className="group relative overflow-hidden rounded-3xl border-2 border-dashed border-blue-200 bg-gradient-to-br from-blue-50/50 to-indigo-50/50 p-12 transition-all hover:border-blue-300 hover:shadow-lg">
      <div className="relative z-10 flex flex-col items-center text-center">
        <div className="mb-6 flex h-24 w-24 items-center justify-center rounded-full bg-blue-100 transition-transform group-hover:scale-110">
          <svg
            xmlns="http://www.w3.org/2000/svg"
            className="h-12 w-12 text-blue-600"
            fill="none"
            viewBox="0 0 24 24"
            stroke="currentColor"
            strokeWidth={1.5}
          >
            <path
              strokeLinecap="round"
              strokeLinejoin="round"
              d="M15.75 6a3.75 3.75 0 11-7.5 0 3.75 3.75 0 017.5 0zM4.501 20.118a7.5 7.5 0 0114.998 0A17.933 17.933 0 0112 21.75c-2.676 0-5.216-.584-7.499-1.632z"
            />
          </svg>
        </div>
        <h3 className="mb-2 text-xl font-bold text-gray-900">
          Создайте свой первый аватар
        </h3>
        <p className="mb-6 max-w-sm text-sm text-gray-600">
          Загрузите фото и создайте цифрового двойника для ваших видео
        </p>
        <button
          type="button"
          onClick={onCreateClick}
          className="rounded-xl bg-blue-600 px-6 py-3 text-sm font-semibold text-white shadow-md transition-all hover:bg-blue-700 hover:shadow-lg active:scale-95"
        >
          Создать аватар
        </button>
      </div>
      <div className="absolute inset-0 opacity-5">
        <div className="absolute left-10 top-10 h-32 w-32 rounded-full bg-blue-400 blur-3xl" />
        <div className="absolute bottom-10 right-10 h-40 w-40 rounded-full bg-indigo-400 blur-3xl" />
      </div>
    </div>
  );
}

